body{
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    padding-bottom: 1000px;
    overflow-x: hidden;
    padding-right:15px;
}
.options{
    background-color: rgb(255, 247, 237);
    border: 1px solid rgb(255, 247, 237);
}
.links {

  position: fixed;
  top: 0;
  left: -2%;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.5s ease-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  z-index: 1; /* Add this line */
}
.c-top-section {
  margin-top: -45px;
  position: relative;
  width: 100vw; /* set the width to be the full width of the viewport */
  height: 900px;
  background-image: url('./chartPGBG.png');
  background-size: cover;
}
.c-square-image {
  position: absolute;
  top: 55%;
  right: 3%;
