/* remove default border and box shadow */
.ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-select-single.ant-select-show-arrow:not(.ant-select-customize-input):hover .ant-select-selector {
  border-color: black;
  box-shadow: none;
}

/* apply custom border */
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  border-radius: 0px;
  border: 1px solid black;
}

/* remove focus highlight */
.ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-select-single.ant-select-show-arrow:not(.ant-select-customize-input):hover .ant-select-selector {
  color: inherit;
  border-color: black;
  box-shadow: none;
}